@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.3/css/bulma.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
  color: #f2f2f2;
}

button {
  background: lightskyblue;
  padding: 6px;
  width: 200px;
  height: 40px;
  color: #fff;
  font-weight: bold;
  border-color: #fff;
  border-radius: 4px;
  border-width: thin;
}

pre {
  background: #1e2429;
  padding: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: 3rem;
  color: #f2f2f2;
  margin-top: 0px;
}

table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 99%;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

@media (max-width: 600px) {
  pre {
    margin: 0;
    margin-right: 1rem;
    margin-top: 1rem;
  }
}

.main-container {
  background-color: white;
  padding-bottom: 1rem;
}

.header {
  background-color: aquamarine;
  padding: 1px;
  padding-left: 16px;
}

.createtemplate-box {
  padding-left: 16px;
}

.folderStructure-box {
  padding-left: 16px;
}

.write-code {
  background-color: #f7f7f7;
  padding: 12px;
}

.api-box {
  display: -webkit-flex;
  display: flex;
}

.fix-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: aqua;
  padding: 16px;
}

.github-link {
  margin-left: 1rem;
}

.point {
  display: list-item;
}

@media (max-width: 600px) {
  .main-container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  .api-box {
    display: block;
  }
  .github-link {
    display: -webkit-flex;
    display: flex;
    margin: 10px;
    margin-top: 20px;
  }
}

